import "bootstrap/dist/css/bootstrap.css";
import "../lib/8.4.0/css/human-authentication-8.4.0.css";
import "./signin.css";

import "angular";
import "angular-route";
import "angular-touch";
import "angular-resource";
import "angular-animate";
import "angular-messages";
import "angular-cookies";

import "bootstrap/dist/js/bootstrap.js";
// NOTE: Special modded version of lib without the gsi lib code.
// This is necessary for loading the code with webpack.
import "../lib/8.4.0/human-authentication-8.4.0-no-gsi";

import "../lib/ga";
import "../lib/amplitude";


var get_param = function (param) {
  var vars = {};
  window.location.href.replace(
      /[?&]+([^=&]+)=?([^&]*)?/gi, // regexp
      function (m, key, value) { // callback
          vars[key] = value !== undefined ? value : '';
      }
  );
  if (param) {
      return vars[param] ? vars[param] : null;
  }
  return vars;
};

window._storage = (function () {
var _storage = window.localStorage || window.chrome.storage || false;
//adapt methods
var _get    = _storage.getItem    || _storage.get,
    _set    = _storage.setItem    || _storage.set,
    _remove = _storage.removeItem || _storage.remove;

var _convert = function (item, set) {
  var _item;

  if(angular.isObject(item)) {
    _item = JSON.stringify(item);
  } else {
    try {
      //if setting JSON string we want to leave the string as is
      _item = set ? item : JSON.parse(item);
    } catch(e) {
      _item = item;
    }
  }

  return _item;
};

return {
  get: function (name) {
    if(_storage && _get) return _convert(_get.call(_storage, name));
  },
  set: function (name, value) {
    if(_storage && _set) _set.call(_storage, name, _convert(value, true));
  },
  remove: function (name) {
    if(_storage && _remove) _remove.call(_storage, name);
  }
};
})();

var authentication_mode = get_param('mode') ? get_param('mode') : 'signin-access';
if (authentication_mode == 'signup') {
  authentication_mode = 'signup-access';
}
else if (authentication_mode == 'signin') {
  authentication_mode = 'signin-access';
}

var go_to_home_page = function () {

  var path = '/#';
  var redirectPath = get_param('redirect_pathname');

  if (redirectPath) {
    path += decodeURIComponent(redirectPath);
  }

  return window.location.replace(path);
};

var path = window.location.pathname.replace('signin.html', '');

var getUserProperties = function (callback) {
$.get('/ws/user/profile', function (response) {

  var locale = window._storage.get('locale');

  var userProperties = {
    firstName: response.first_name,
    lastName: response.last_name,
    email: response.email,
    locale: locale ? locale.id : 'en_US'
  };

  callback(response.id, userProperties);
});
};

HumanAuthenticationAPI.setup('dropin', {
  containerId: 'auth-container',
  authentication: {
      type: authentication_mode,
      platform: "smiletrain",
      productKey: 'wa_A7oED7aW32',
      productType: 'web',
      version: "",
      hostedApp: true,
      recaptchaV3: {
          active: true,
          show_badge: false
      },
      feedbackModalDialog: {
          active: false
      }
 },
  controls: {
      logo: {
          show: true,
          href: path + '#/',
      },
      thirdParty:
      {
          google: {
              show: false
          }
      },
      privacyPolicy: {
          show: true,
          href: path + '#/privacy',
      },
      termsOfService: {
          show: true,
          href: path + '#/tos',
      },
      verificationCode:
      {
          verificationCodeInputMode: process.env.MOBILE ? "single-box" : "multi-box" // ["single-box", "multi-box"]
      }
  },
  transitions: {
      animate: false,
      style: 'slideInRight'
  },
  onContainerReady: function () {
      ga('send', 'pageview', window.location);
  },
  onContainerError: function () {
  },
  onFormChange: function () {
  },
  onSigninSuccess: function () {

    getUserProperties(function (userId, userProperties) {
      amplitude.getInstance().setUserId(userId);
      amplitude.getInstance().setUserProperties(userProperties);
      amplitude.getInstance().logEvent('login');

      go_to_home_page();
    });

  },
  onSigninError: function () {
  },
  onSignupSuccess: function () {

    getUserProperties(function (userId, userProperties) {
      amplitude.getInstance().setUserId(userId);
      amplitude.getInstance().setUserProperties(userProperties);
      amplitude.getInstance().logEvent('signup');

      window._storage.set('new-user', 'true');

      go_to_home_page();
    });

  },
  onSignupError: function () {
  },
  isSignedIn: function () {
      go_to_home_page();
  }
});

window.googleSignInSuccess = function() {
  getUserProperties(function (userId, userProperties) {
    amplitude.getInstance().setUserId(userId);
    amplitude.getInstance().setUserProperties(userProperties);
    amplitude.getInstance().logEvent('google sign in success');

    go_to_home_page();
  });
}

window.skipSignIn = function() {
  getUserProperties(function (userId, userProperties) {
    amplitude.getInstance().setUserId(userId);
    amplitude.getInstance().setUserProperties(userProperties);
    amplitude.getInstance().logEvent('skip log in');

    go_to_home_page();
  });
}
