'use strict';

angular.module('humanAuthentication.submitFacebookV2', [])

    .directive('humanSubmitFacebookV2', ['$rootScope', '$compile', '$window', function ($rootScope, $compile, $window) {
        return {
            restrict: 'E',
            replace: true,
            scope: true,
            templateUrl: 'templates/controls/submit_facebook_v2.html',
            link: function (scope, element, attrs) {                           
                /**
                 * Initializes the Sign-In client.
                 */

                var initClient = function () {

                    var statusChangeCallback = function (response) {  // Called with the results from FB.getLoginStatus().
                        console.log('statusChangeCallback');
                        console.log(response);                   // The current login status of the person.
                        if (response.status === 'connected') {   // Logged into your webpage and Facebook.
                            //console.log('Welcome!  Fetching your information.... ');
                            //$window.FB.api('/me', function (response) {
                            //    console.log('Successful login for: ' + response.name);
                            //    console.log("response: ", response);
                            //});
                            //$window.FB.logout(function (response) {
                            //    // user is now logged out
                            //    console.log("logged facebook user out");
                            //});
                            //onSuccess(response);
                        } else {// Not logged into your webpage or we are unable to tell.
                        }
                    };        

                    var checkLoginState = function () { // Called when a person is finished with the Login Button.
                        $window.FB.getLoginStatus(function (response) {   // See the onlogin handler
                            statusChangeCallback(response);
                        }, { auth_type: 'reauthenticate' });
                    };    

                    element.bind('click', function () {
                        $window.FB.login(function (response) {
                            if (response.authResponse) {
                                onSuccess(response);
                            } else {
                                console.log('User cancelled login or did not fully authorize.');
                                onFailure({ error: 'User cancelled login or did not fully authorize.' });
                            }
                        }, { scope: 'public_profile,email', auth_type: 'reauthenticate' });
                    });

                    $window.FB.init({
                        appId: scope.config.controls.thirdParty.facebook.api_key,
                        cookie: true,                     // Enable cookies to allow the server to access the session.
                        xfbml: true,                     // Parse social plugins on this webpage.
                        version: scope.config.controls.thirdParty.facebook.api_version           // Use this Graph API version for this call.
                    });  

                    $window.FB.getLoginStatus(function (response) {   // Called after the JS SDK has been initialized.
                        statusChangeCallback(response);        // Returns the login status.
                    }, { auth_type: 'reauthenticate' });

                };

                /**
                 * Handle successful sign-ins.
                 */
                var onSuccess = function (response) {
                    $rootScope.$broadcast(scope.config.controls.thirdParty.facebook.providerSuccessBroadcastName, { data: response });
                };

                /**
                 * Handle sign-in failures.
                 */
                var onFailure = function (response) {
                    $rootScope.$broadcast(scope.config.controls.thirdParty.facebook.providerErrorBroadcastName, { data: response });
                }; 
                initClient();
            }
        };
    }])

    .run(['$rootScope', function ($rootScope) {

    }]);

