angular.module('humanAuthentication.submitGoogleV2', [])

    .directive('humanSubmitGoogleV2', ['$rootScope', '$compile', '$window', function ($rootScope, $compile, $window) {
  return {
    restrict: 'E',
    replace: true,
    scope: true,
    templateUrl: 'templates/controls/submit_google_v2.html',
    link: function (scope, element, attrs) {

        var divBtn = element.find('div');

        /**
         * Initializes the Sign-In client.
         */
        var initClient = function () {
            $window.gapi.load('auth2', function () {
                /**
                 * Retrieve the singleton for the GoogleAuth library and set up the
                 * client.
                 */
                var auth2 = $window.gapi.auth2.init({
                    client_id: scope.config.controls.thirdParty.google.client_id,
                    fetch_basic_profile: true,
                    ux_mode: 'popup'
                });

                // Attach the click handler to the sign-in button
                auth2.attachClickHandler(
                    divBtn.attr("id"),
                    { prompt: 'select_account' },
                    onSuccess,
                    onFailure
                );

            });
        };

        /**
         * Handle successful sign-ins.
         */
        var onSuccess = function (user) {
            $rootScope.$broadcast(scope.config.controls.thirdParty.google.providerSuccessBroadcastName, { data: user });
        };

        /**
         * Handle sign-in failures.
         */
        var onFailure = function (response) {
            $rootScope.$broadcast(scope.config.controls.thirdParty.google.providerErrorBroadcastName, { data: response });
        };

        initClient();
    }
  };
}])

.run(['$rootScope', function ($rootScope) {

}]);

