'use strict';

angular.module('humanAuthentication.submitGoogleV3', [])

    .directive('humanSubmitGoogleV3', ['$rootScope', '$compile', '$window', function ($rootScope, $compile, $window) {
  return {
    restrict: 'E',
    replace: true,
    scope: true,
    templateUrl: 'templates/controls/submit_google_v3.html',
    link: function (scope, element, attrs) {

        //var divBtn = element.find('div');
        var gsiBtn = document.getElementById("gsi_id_signin_btn");
        var customBtn = document.getElementById("submit_google_v3_btn");
        var notice = '';

        /**
         * Initializes the Sign-In client.
         */
        var initClient = function () {            

            if (!$window.google){

                notice = 'Google gsi client not detected.';
                console.log(notice);
                scope.showDialogError(notice);
                scope.config.controls.thirdParty.google.disabled = true;           
            }
            else {

                notice = 'Google gsi client detected.';
                console.info(notice);                
                scope.config.controls.thirdParty.google.disabled = false;

                // Google's rendered button will be set to display: none
                // will use a custom button to activate the hidden rendered btn
                customBtn.addEventListener('click', () => {
                    document.querySelector('.gsi_id_signin div[role=button]').click();
                });            

                var auth2 = $window.google.accounts.id.initialize({
                    client_id: scope.config.controls.thirdParty.google.client_id,
                    context: "signin",
                    ux_mode: "popup",
                    use_fedcm_for_prompt: true,
                    callback: handleCredentialResponse                
                });
            
                // Google's rendered button will be set to display: none
                // will show a custom button instead
                $window.google.accounts.id.renderButton(
                    gsiBtn,
                    // customization attributes
                    { 
                        theme: "outline",
                        size: "large" 
                    }  
                );            
                $window.google.accounts.id.prompt((notification) => {
                    if(notification.isSkippedMoment()) {
                    //console.log("Prompt cancelled by user");
                    }
                });         
            }

        };

        /**
         * Handle successful sign-ins.
         */
        var onSuccess = function (user) {
            $rootScope.$broadcast(scope.config.controls.thirdParty.google.providerSuccessBroadcastName, { data: user });
        };

        /**
         * Handle sign-in failures.
         */
        var onFailure = function (response) {
            $rootScope.$broadcast(scope.config.controls.thirdParty.google.providerErrorBroadcastName, { data: response });
        };

         /**
         * Handle callback.
         */
         var handleCredentialResponse = function (credential_response) {

            try { 
    
                var googleUser = {
                    'id_token' : credential_response.credential,
                    'select_by' : credential_response.select_by,
                    'header' : null,
                    'profile' : null
                };

                if (!$window.KJUR){

                    notice = 'RSA-Sign JavaScript Library (jsrsasign) not detected.';
                    console.log(notice);
                    scope.showDialogError(notice);
                    scope.config.controls.thirdParty.google.disabled = true; 
                }
                else if (!$window.b64utoutf8){

                    notice = 'b64utoutf8 method not detected.';
                    console.log(notice);
                    scope.showDialogError(notice);
                    scope.config.controls.thirdParty.google.disabled = true; 
                }
                else {
                    
                    googleUser['header']  = $window.KJUR.jws.JWS.readSafeJSONString($window.b64utoutf8(credential_response.credential.split(".")[0]));
                    googleUser['profile']  = $window.KJUR.jws.JWS.readSafeJSONString($window.b64utoutf8(credential_response.credential.split(".")[1]));       
                    onSuccess(googleUser);   
                }
            
            }
            catch (ex) {
                var response = {'data' : {'error' : ex}};
                onFailure(response);
             }
        };       

        initClient();
    }
  };
}])

.run(['$rootScope', function ($rootScope) {

}]);

