angular.module('humanAuthentication.userVerificationCodeMultiInput', [])

.directive('humanUserVerificationCodeMultiInput', ['$rootScope', function ($rootScope) {
  return {
    restrict: 'E',
    replace: true,
    scope: true,
    templateUrl: 'templates/controls/verification-code-multi-input.html',
      link: function (scope, element, attrs) {


          var valCodeInput = angular.element($('#verification-code-input-multi')); //element.find('input');
          var valCodeItems = element.find('div[name="verification-code-item"]');
          var valCodeLength = 0;
          var i = 0;

          var update_input_element = function () {
              valCodeLength = valCodeInput.val().length;
              if (valCodeInput.val()) {

                  for (i = 0; i < valCodeItems.length; i++) {
                      if (valCodeInput.val()[i]) {
                          $(valCodeItems[i]).addClass('available');
                          $(valCodeItems[i]).text(valCodeInput.val().substring(i, i + 1));
                      }
                      else {
                          $(valCodeItems[i]).removeClass('available');
                          $(valCodeItems[i]).text("");
                      }
                  }
              }
              else {
                  for (i = 0; i < valCodeItems.length; i++) {
                      $(valCodeItems[i]).removeClass('available');
                      $(valCodeItems[i]).text("");
                  }

              }
          }; 

          valCodeInput.on('input propertychange change', function (e) {
              update_input_element();
          });  
          valCodeInput.on('keyup', function (e) {
              if (e.keyCode === 8) {
                  $(valCodeItems[valCodeLength]).removeClass('available');
                  $(valCodeItems[valCodeLength]).text("");
              }
          });
          scope.$on('clearVerificationCodeInput', function (e, response) {
              valCodeInput.val('');
              update_input_element();
              valCodeInput.focus();
          });                                                     

          valCodeInput.val('');
          valCodeInput.focus();         

    }
  };
}])

.run(['$rootScope', function ($rootScope) {

}]);

