    $scope.$on('onGoogleUserSigninError', function (e, response) {

        var data = response.data;
        var notice = '<br>Uh oh, we encountered an google api service related error.  Please try again and contact us if the issue continues.';

        if (data && data.error) {
            if (data.error == "popup_blocked_by_browser") {
                notice = "Make sure your browser is not blocking popups.";
            }
            else if (data.error == "popup_closed_by_user") {
                notice = "The user closed the popup before finishing the consent flow.";
            }
            else if (data.error == "access_denied") {
                notice = "The user denied the permission to the scopes required.";
            }
            else if (data.error == "immediate_failed") {
                notice = "No user could be automatically selected without prompting the consent flow.";
            }
            else {
                notice = '<br>Uh oh, we encountered an google api service related error:  ' + data.error+'.  Please try again and contact us if the issue continues.';
            }
        }
        
        $timeout(function () {
            $scope.authLoader(false);
            $scope.config.controls.standard.submit.disabled = false;
            $scope.hideWaitingWindow();
            $scope.showDialogError(notice);
        });    

    });

