    $scope.processSectionContactSales = function (isValid, user, errors) {
          
        if (isValid) {

            $scope.config.controls.standard.submit.disabled = true;
            $scope.showWaitingWindow('Processing ...');

            // check if existing or new account
            var contactSalesSuccess = function (response) {

                var data = response ? response.data : null;
                var status = response ? response.status : null;
                var headers = response ? response.headers : null;
                var config = response ? response.config : null;
                var statusText = response ? response.statusText : null;

                //var ws_data = {
                //    'developer_id': user.developer.developer_id,
                //    'mode': 'sent_to_crm'
                //};
                //var ws_obj = {
                //    'data': ws_data
                //};
                //ws_obj.success = function () { };
                //ws_obj.error = function () { };
                //UserAccount.developerActivity(ws_obj);


                var nextSectionName = $scope.signinAccessSection.contact_sales_confirmation.name;
                var nextSectionPath = $scope.signinAccessSection.contact_sales_confirmation.path;
                var nextSectionMode = "";
                $scope.goToNextSection(nextSectionName, nextSectionPath, nextSectionMode);

            };
            var ContactSalesError = function (response) {

                var data = response ? response.data : null;
                var status = response ? response.status : null;
                var headers = response ? response.headers : null;
                var config = response ? response.config : null;
                var statusText = response ? response.statusText : null;

                $scope.config.controls.standard.submit.disabled = false;
                $scope.hideWaitingWindow();

                $timeout(function () {
                    var notice;

                    switch (status) {
                        case 400:
                            notice = status + " Bad Request. The request is not properly formatted, doesn't properly specify response format or contains improper parameters or data.  " + data.error.message;
                            break;
                        case 401:
                            notice = status + " Unauthorized. Authorization data is absent, invalid or expired. Generally this means that a login form should be displayed to a user.  " + data.error.message;
                            break;
                        case 403:
                            notice = status + " Forbidden. Issued when user is logged in, but does not have permission for requested operation. This includes cases when user has reached limits like allowed number of contacts.  " + data.error.message;
                            break;
                        case 404:
                            notice = status + " Resource Not Found. When an id was provided for a request but no resource exists for that object.  " + data.error.message;
                            break;
                        case 409:
                            notice = status + " Precondition Failed. Server state does not allow requested operation to be carried on. Issued in cases such as when trying to add an assigned next action to a contact which already has one assigned for a user.  " + data.error.message;
                            break;
                        default:
                            notice = '<br>Uh oh, we encountered an internal service related error.  Please try again and contact us if the issue continues.';
                    }
                    $scope.authLoader(false);
                    //$scope.signinNotice(true, notice);
                    $scope.showDialogError(notice);
                    DoCallback.container.error(notice);
                });
            };

            var organization_name = user.developer.organization_details ? user.developer.organization_details.name : '';

            var organization_account_company_website = user.developer.organization_details ? user.developer.organization_details.group_attributes.filter(x => x.name === "account_company_website") : '';
            organization_account_company_website = organization_account_company_website.length ? organization_account_company_website[0].value : '';

            var organization_account_company_size = user.developer.organization_details ? user.developer.organization_details.group_attributes.filter(x => x.name === "account_company_size") : '';
            organization_account_company_size = organization_account_company_size.length ? organization_account_company_size[0].value : '';

            var organization_account_purpose = user.developer.organization_details ? user.developer.organization_details.group_attributes.filter(x => x.name === "account_purpose") : '';
            organization_account_purpose = organization_account_purpose.length ? organization_account_purpose[0].value : '';

            var organization_account_field = user.developer.organization_details ? user.developer.organization_details.group_attributes.filter(x => x.name === "account_field") : '';
            organization_account_field = organization_account_field.length ? organization_account_field[0].value : '';

            var organization_account_user_role = user.developer.organization_details ? user.developer.organization_details.user_role.role_display_name : '';

            var account_purpose = user.developer.user_details.user_attributes.filter(x => x.name === "account_purpose");
            account_purpose = account_purpose.length ? account_purpose[0].value : '';

            var account_field = user.developer.user_details.user_attributes.filter(x => x.name === "account_field");
            account_field = account_field.length ? account_field[0].value : '';

            var account_role = user.developer.user_details.user_attributes.filter(x => x.name === "account_role");
            account_role = account_role.length ? account_role[0].value : '';

            var contact_source = '';

            var description = "API Developer Inquiry.  ";
            description += '\n\Details for this inquiry';
            description += '\n* Inquiry source: ' + 'developer portal "contact solutions expert" form';
            description += '\n* Inquiry reason: ' + user.contactSales.intent;
            description += '\n* Inquiry comments: ' + user.requestAccess.organizationComments;
            description += '\n\Account details';
            description += '\n* Organization name: ' + (organization_name ? organization_name : '[Not associated with a team account]');
            description += '\n* Organization company website: ' + organization_account_company_website;
            description += '\n* Organization company size: ' + organization_account_company_size;
            description += '\n* Organization user role: ' + organization_account_user_role;
            description += '\n* Account profile purpose: ' + account_purpose;
            description += '\n* Account profile field: ' + account_field;
            description += '\n* Account profile role: ' + account_role;

            var ws_data = {
                'contact': {
                    'first_name': user.first_name,
                    'last_name': user.last_name,
                    'company': organization_name,
                    'description': description,
                    'industry': organization_account_field ? organization_account_field : account_field,
                    'email': user.email || '',
                    'website': organization_account_company_website || '',
                    'lead_source': 'Inbound Lead',
                    'lead_source_detail': 'Developer Request'
                }
            };
            var ws_obj = {
                'data': ws_data
            };
            var nextSectionName = $scope.signinAccessSection.contact_sales_confirmation.name;
            var nextSectionPath = $scope.signinAccessSection.contact_sales_confirmation.path;
            var nextSectionMode = "";
            $scope.goToNextSection(nextSectionName, nextSectionPath, nextSectionMode);

            ws_obj.success = contactSalesSuccess;
            ws_obj.error = ContactSalesError;
            UserAccount.developerContactSales(ws_obj);
        }
        else {

        }
    };

