    $scope.signupSubmit = function (authForm, isValid, user) {
        // Set the 'submitted' flag to true
        $scope.signinSubmitted = true;

        if (isValid) {     

            if (user.password !== user.passwordConfirm) {
                var msg = "<p>Passwords do not match.  Please try again.</p>";
                $scope.showDialogError(msg);
                return;
            }

            $scope.config.controls.standard.submit.disabled = true;
            $scope.showWaitingWindow('Processing ...');

            var complete = function (recaptcha_response) {
                var nameArray = UserUtils.getFirstAndLastName(user.name);
                var ws_obj = {};

                var email_confirmation = $scope.config.authentication.signup.email_confirmation.active;
                var email_confirmation_app_templatename = $scope.config.authentication.signup.email_confirmation.customAppTemplateName;
                var email_confirmation_app_link = $scope.config.authentication.signup.email_confirmation.customAppLink;
                var email_confirmation_app_displayname = $scope.config.authentication.signup.email_confirmation.customAppDisplayName;

                var ws_data = {
                    'email': user.email,
                    'password': user.password,
                    'name': user.name,
                    'first_name': nameArray[0] ? nameArray[0] : '',
                    'last_name': nameArray[1] ? nameArray[1] : '',
                    'grade_level': '',
                    'site_verify_response': recaptcha_response ? JSON.stringify(recaptcha_response) : null
                };

                if (Verify.isGroupMode()) {
                    ws_data['group_code_mode'] = true;
                    ws_data['group_code'] = user.groupCode;
                    ws_data['group_id'] = $scope.groupId ? $scope.groupId : '';
                }
                else {
                    ws_data['group_code_mode'] = false;
                    ws_data['group_code'] = '';
                    ws_data['group_id'] = $scope.groupId ? $scope.groupId : '';
                }
                if (email_confirmation === true) {
                    ws_data['email_confirmation'] = "1";
                    ws_data['email_confirmation_app_templatename'] = email_confirmation_app_templatename ? email_confirmation_app_templatename : '';
                    ws_data['email_confirmation_app_link'] = email_confirmation_app_link ? email_confirmation_app_link : '';
                    ws_data['email_confirmation_app_displayname'] = email_confirmation_app_displayname ? email_confirmation_app_displayname : '';
                }
                else {
                    ws_data['email_confirmation'] = "0";
                    ws_data['email_confirmation_app_templatename'] = '';
                    ws_data['email_confirmation_app_link'] = '';
                    ws_data['email_confirmation_app_displayname'] = '';
                }
                if (Verify.isPlatform.developer() || Verify.isPlatform.hostedApp()) {
                    ws_data['redirect_pathname'] = angular.equals($scope.config.authentication.redirectMode.active, false) ? '0' : $scope.config.authentication.redirectMode.path;
                    //ws_data['persist_options'] = SEARCH_PARAMETERS_STRING;
                    ws_data['platform'] = $scope.config.authentication.platform;
                }
                if (Verify.isGroupInvitationMode()) {
                    ws_data['group_invite_mode'] = 1;
                    ws_data['group_invite_key'] = $scope.config.authentication.groupInvitationMode.groupInviteKey;
                }

                var signupStandardSuccess = function (response) {

                    var data = response.data;
                    var status = response.status;
                    var headers = response.headers;
                    var config = response.config;
                    var statusText = response.statusText;

                    var nextSectionName = "";
                    var nextSectionPath = "";
                    var nextSectionMode = "";
                    var notice = "";

                    //rembmer new user
                    var key = $scope.config.constants.NEW_USER_KEY;

                    if (!Verify.isPlatform.human()) {
                        key = $scope.config.authentication.platform ? $scope.config.constants.NEW_USER_KEY + '_' + $scope.config.authentication.platform : key;
                    }
                    Storage.set(key, $scope.config.constants.NEW_USER_KEY);

                    $timeout(function () {
                        $scope.config.controls.standard.submit.disabled = false;
                        $scope.hideWaitingWindow();

                        if (data) {
                            if (data.verification_required) {

                                $scope.user.email = data.user_email;
                                $scope.user.verification.required = data.verification_required;
                                $scope.user.verification.jwt = data.verification_jwt;
                                $scope.user.verification.redirect_on_success_fn = null;

                                nextSectionName = $scope.signupAccessSection.email_verification.name;
                                nextSectionPath = $scope.signupAccessSection.email_verification.path;
                                nextSectionMode = "";
                                $scope.goToNextSection(nextSectionName, nextSectionPath, nextSectionMode);

                            }
                            else {

                                if (data.signed_in) {

                                    if (Verify.isGroupInvitationMode()) {
                                        var on_invite_complete = function (group_invite_status) {
                                            DoCallback.signinSuccess.standard("", group_invite_status);
                                            $scope.authLoader(true, "Signin Success");
                                        };
                                        UserUtils.handleGroupInviteStatus(data, on_invite_complete);
                                    }
                                    else {
                                        DoCallback.signupSuccess.standard();
                                        $scope.authLoader(true, "Signup Success");
                                    }

                                }

                                else {

                                    notice = 'Sorry, but we could not sign you up.  (e.us.v3)';
                                    $scope.authLoader(false);
                                    $scope.showDialogError(notice);
                                    $scope.config.controls.standard.submit.disabled = false;
                                    $scope.hideWaitingWindow();
                                    DoCallback.signupError.standard(notice);
                                }
                            }
                        }
                        else {
                            notice = 'Sorry, but we may have had an issue with signing you up.  (e.us.v3)';
                            $scope.authLoader(false);
                            $scope.showDialogError(notice);
                            $scope.config.controls.standard.submit.disabled = false;
                            $scope.hideWaitingWindow();
                            DoCallback.signupError.standard(notice);
                        }


                    });
                };

                var signupStandardError = function (response) {

                    var data = response.data;
                    var status = response.status;
                    var headers = response.headers;
                    var config = response.config;
                    var statusText = response.statusText;

                    $timeout(function () {
                        var notice;

                        if (data && data.error && data.error.message) {
                            notice = data.error.message;
                        }
                        else {
                            notice = 'Uh oh, we encountered an internal service related error.  ';
                            notice += 'Please try again or contact us if the issue continues.';
                        }

                        $scope.authLoader(false);
                        $scope.showDialogError(notice);
                        $scope.config.controls.standard.submit.disabled = false;
                        $scope.hideWaitingWindow();
                        DoCallback.signupError.standard(notice);
                    });
                };

                ws_obj.data = ws_data;
                ws_obj.success = signupStandardSuccess;
                ws_obj.error = signupStandardError;

                UserAccount.standardSignup(ws_obj);
                $scope.resetForm(authForm);
            };  

            if (angular.equals($scope.config.authentication.recaptchaV3.active, true) && angular.equals(gRecaptcha.isInitialized(),true)) {
                RecaptchaV3.verify(gRecaptcha, complete, $scope.config.constants.AUTH_TYPE_SIGNUP_ACCESS);
            }
            else { complete(); }         

        }
        else {

        }
    };

    $scope.signinStandardEncrypted = function (user) {

        $scope.showWaitingWindow('Validating ...');

        var complete = function (recaptcha_response) {
            var ws_obj = {};

            var credentials = {
                'user_eid': user.eid,
                'user_email': user.user_email,
                'authentication_type': 'signin',
                'authentication_provider': user.user_third_party_provider
            };
            var base64EncodedString = $base64.encode(JSON.stringify(credentials));
            var urlSafeBase64EncodedString = encodeURIComponent(base64EncodedString);

            var ws_data = {
                'credentials': urlSafeBase64EncodedString,
                'platform': user.user_email,
                'authentication_type': 'signin',
                'authentication_provider': user.user_third_party_provider,
                'site_verify_response': recaptcha_response ? JSON.stringify(recaptcha_response) : null
            };

            if (Verify.isPlatform.hostedApp()) {
                ws_data['platform'] = $scope.config.authentication.platform;
            }

            var signinEncryptedSuccess = function (response) {

                var data = response.data;
                var status = response.status;
                var headers = response.headers;
                var config = response.config;
                var statusText = response.statusText;

                $scope.config.controls.standard.submit.disabled = false;
                $scope.hideWaitingWindow();

                if (Verify.isGroupInvitationMode()) {
                    var on_invite_complete = function (group_invite_status) {
                        if (user.user_is_third_party === true) {
                            DoCallback.signinSuccess.thirdParty("", user.user_third_party_provider, group_invite_status);
                        }
                        else {
                            DoCallback.signinSuccess.standard("", group_invite_status);
                        }
                        $scope.authLoader(true, "Signin Success");
                    };
                    UserUtils.handleGroupInviteStatus(data, on_invite_complete);
                }
                else {
                    if (user.user_is_third_party === true) {
                        DoCallback.signinSuccess.thirdParty("", user.user_third_party_provider);
                    }
                    else {
                        DoCallback.signinSuccess.standard();
                    }
                    $scope.authLoader(true, "Signin Success");
                }


            };
            var signinEncryptedError = function (response) {

                var data = response.data;
                var status = response.status;
                var headers = response.headers;
                var config = response.config;
                var statusText = response.statusText;

                $timeout(function () {
                    var notice;

                    if (data && data.error && data.error.message) {
                        notice = data.error.message;
                    }
                    else {
                        notice = 'Uh oh, we encountered an internal service related error.  ';
                        notice += 'Please try again or contact us if the issue continues.';
                    }

                    $scope.authLoader(false);
                    $scope.showDialogError(notice);
                    $scope.config.controls.standard.submit.disabled = false;
                    $scope.hideWaitingWindow();
                    if (user.user_is_third_party === true) {
                        DoCallback.signupError.thirdParty("", user.user_third_party_provider);
                    }
                    else {
                        DoCallback.signupError.standard(notice);
                    }

                });

            };

            ws_obj.data = ws_data;
            ws_obj.success = signinEncryptedSuccess;
            ws_obj.error = signinEncryptedError;

            UserAccount.standardSignin(ws_obj);
        };

        if (angular.equals($scope.config.authentication.recaptchaV3.active, true) && angular.equals(gRecaptcha.isInitialized(),true)) {
            RecaptchaV3.verify(gRecaptcha, complete, $scope.config.constants.AUTH_TYPE_SIGNUP_ACCESS);
        }
        else { complete(); }         

    };


